import React, { Component } from 'react';
import './App.css';

import { getUsers, getTodos } from './api/getData';
import TodosList from './TodosList';
import NewTodo from './NewTodo';

class App extends Component {
  state = {
    users: [],
    copyUsers: [],
    todos: [],
    visibleTodos: [],
    copyVisibleTodos: [],
  };

  async componentDidMount() {
    const users = await getUsers();
    const todos = await getTodos();

    this.setState({
      users,
      copyUsers: users,
      todos,
      visibleTodos: todos.map((todo, index) => ({
        ...todo,
        user: users.find(user => todo.userId === user.id),
        numId: todo.id
      })),
      copyVisibleTodos: todos.map((todo, index) => ({
        ...todo,
        user: users.find(user => todo.userId === user.id),
        numId: todo.id
      }))
    });
  }

  handleSearch = (event) => {
    const {value} = event.target;
    const searchVal = value.toLowerCase();

    this.setState(prevState => ({
      visibleTodos: prevState.copyVisibleTodos.filter(todo =>
        [...todo.title]
          .join('')
          .toLowerCase()
          .includes(searchVal)
      ),
    }));
  };

  onSortByName = (event) => {
    event.preventDefault();
    const { value } = event.target;
    // console.log(value);

    this.setState(prevState => ({
      visibleTodos: prevState.copyVisibleTodos.filter(
        todo => todo.user.name === value
      ),
      copyUsers: prevState.users.filter(user => user.name === value)
    }));
  }

  render() {
    const { copyUsers, users, visibleTodos } = this.state;
    console.log(this.state.visibleTodos.filter( todo=> todo.user.name=== "Leanne Graham"))

    console.log(visibleTodos);

    return (
      <div className="App">
        <h1>TODO LIST</h1>
        <div className="search">
          <input
            className="search-field"
            type="input"
            autoFocus
            placeholder="search..."
            onChange={this.handleSearch}
          />
        </div>
        <NewTodo
          data={visibleTodos}
          users={users}
          key={visibleTodos.map(todo => todo.id)}
        />
        <span>Users:</span> {copyUsers.length}
        {", "}
        <span>Todos:</span> {visibleTodos.length}{" "}
        <p>
          <span>completed todos: </span>
          {visibleTodos.filter(todo => todo.completed === true).length}
          <br />
          <span>todos in progres: </span>
          {visibleTodos.filter(todo => todo.completed === false).length}
        </p>
        <div className="table-box">
          <table className="">
            <thead>
              <tr>
                <td>№</td>
                <td>Status</td>
                <td>Task</td>
                <td>
                  <select onChange={this.onSortByName}>
                    <option value="Leanne Graham">...</option>
                    {users.map(user => (
                      <option
                        onChange={this.onSortByName}
                        value={user.name}
                        key={user.id}
                      >
                        {user.name}
                      </option>
                    ))}
                  </select>
                </td>
              </tr>
            </thead>
            {visibleTodos.map(todo => (
              <TodosList todo={todo} key={todo.id} />
            ))}
          </table>
        </div>
      </div>
    );
  }
}

export default App;
