import React, { Component } from 'react';

class NewTodo extends Component {
  state = {
    valuesMap: {
      id: '',
      name: '',
      title: '',
      completed: false,
    },

    errorsMap: {
      name: '',
      title: '',
    },
  };

  handleChange = (event) => {
    event.preventDefault();
    const { value, name } = event.target;
    const { valuesMap, errorsMap } = this.state;

    this.setState({
      valuesMap: {
        ...valuesMap,
        [name]: value,
      },
    });
  }

  handleSubmit = (event) => {
    const { value, name } = event.target;

    event.preventDefault();
    this.setState(prevState => ({
      visibleTodos: {
        ...prevState.visibleTodos,
        ...this.valuesMap,
      },
      [name]: value,
    }));
  }

  render() {
    const { valuesMap, errorsMap } = this.state;
    const { users, data } = this.props;

    console.log(this.state.visibleTodos);
    console.log(data);

    return (
      <form onSubmit={this.handleSubmit}>
        <label htmlFor="todo">
          Todo:
          <input
            className="task-field"
            id="todo"
            name="title"
            placeholder="Enter task"
            onChange={this.handleChange}
            value={valuesMap.title}
          />
        </label>
        {' '}
        <select name="name" onChange={this.handleChange}>
          <option defaultValue name="name" value="none">
            choose name...
          </option>
          {users.map(user => (
            <option name="name" value={user.name} key={user.name}>
              {user.name}
            </option>
          ))}
        </select>
        {' '}
        <button type="submit">Add TODO</button>
      </form>
    );
  }
}

export default NewTodo;
